.wrapper {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.row {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.name {
    font-family: Pretendard-Regular;
    font-size: var(--font-size-15);
    font-weight: 600;
}

.value {
    font-family: Pretendard-Regular;
    font-size: var(--font-size-16);
    font-weight: 400;
    color: var(--color-black);
}
