.gridItem {
    width: 100%;
    padding-top: 50%; /* 1:1 비율의 정사각형을 만들기 위해 너비와 동일한 패딩 값을 설정합니다. */
    position: relative;
    background-color: #f5f5f5;
    background-color: var(--color-white);
}

.squareContentNumber {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Pretendard-Regular;
    font-size: var(--font-size-17);
    font-weight: 700;

}
.squareContentIcon {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 8px;
    right: 8px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}
.available {
    background-color: var(--color-white);
    color: var(--color-black);
}

.unavailable {
    background-color: var(--color-light-grey);
    color: var(--color-grey);
}

.maintenance {
    background-color: var(--color-light-grey);
    color: var(--color-grey);
}
.selected {
    color: var(--color-white);
    background: var(--color-black);
}
.selectedIcon {
    bottom: 16px;
    right: 16px;
}
