.wrapper{
    width: 100%;
    min-width: 0;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    padding: 24px 0;
    font-family: Pretendard-Regular;
    border-top: 1px solid var(--color-light-grey);
    border-bottom: 1px solid var(--color-light-grey);
    gap: 4px;
    position: relative;
}
.fcHeader {

    display: flex;
    gap: 8px;
    align-items: center;
    margin-bottom: 1px;
}
.fcHeader .name {
    font-size: var(--font-size-17);
    font-weight: 600;
}
.fcHeader .status {
    font-size: var(--font-size-12);
    font-weight: 600;
    color: var(--color-primary);
    border: 1px solid var(--color-primary);
    border-radius: var(--font-size-12);
    padding: 4px 8px;
}
.fcResDate {
    font-size: var(--font-size-16);
    font-weight: 400;
    color: var(--color-grey);
}
.fcDetail {
    font-size: var(--font-size-16);
    font-weight: 400;
    color: var(--color-grey);
}
.button {
    position: absolute;
    right: 0;
    top: calc(50% - 16px);
}
