/* Button.module.css */
.buttonWrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
    font-family: Pretendard-Regular;
    font-size: var(--font-size-17);
    font-weight: 700;
    text-transform: uppercase;
    transition: background-color 0.3s ease-in-out;
    border-radius: var(--border-radius-16);
    height: 60px;
    gap: 8px;
}

.buttonWrapper:hover {
    opacity: 0.8;
}

