.wrapper {
    width: 50%;
    min-width: 0;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    padding: 24px 16px;
    font-family: Pretendard-Regular;
    gap: 4px;
    position: relative;
}
.title {
    font-family: Pretendard-Regular;
    font-size: var(--font-size-17);
    font-weight: 600;
    margin-bottom: 8px;
}
.countWrap {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}
.numerator {
    font-family: Pretendard-Regular;
    font-size: var(--font-size-27);
    font-weight: 600;
    color: var(--color-primary);
}
.denominator {
    font-family: Pretendard-Regular;
    font-size: var(--font-size-19);
    font-weight: 400;
    color: var(--color-grey);
    margin-left: 4px;
}
