.wrapper {
    width: 100%;
    min-width: 0;
    display: flex;
    padding: 32px 0;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid var(--color-light-grey);
    font-family: Pretendard-Regular;
}
.key {
    font-size: var(--font-size-19);
    font-weight: 600;
    margin: 0 16px 0 16px;
}
.value {
    font-size: var(--font-size-16);
    font-weight: 400;
    margin: 0 16px 0 0;
}
