@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

:root {
    --color-bg-dark: #f5f5f5;
    --color-bg: #fdfffd;
    --color-grey: #d1d1d1;
    --color-text: #22243b;
    --color-accent: #f16e03;
    --color-white: white;
    --color-scrollbar: #aaa7a7;

    --bg-shape-color: linear-gradient(120deg, #343A4F, #0F1620);
    --lightblue: #3D9DEA;
    --darkblue: #4A4EEE;
    --text-color: #D5E1EF;

    --color-primary: #27A77A;
    --color-red: #EE5050;
    --color-black: #282A29;
    --color-grey: #C9CAC9;
    --color-light-grey: #F4F5F7;
    --color-bg-grey: #E9EAEA;

    --font-size-27: 1.6875rem;
    --font-size-24: 1.5rem;
    --font-size-22: 1.375rem;
    --font-size-21: 1.3125rem;
    --font-size-20: 1.25rem;
    --font-size-19: 1.1875rem;
    --font-size-17: 1.0625rem;
    --font-size-16: 1rem;
    --font-size-15: 0.9375rem;
    --font-size-13: 0.8125rem;
    --font-size-12: 0.75rem;
    --font-size-11: 0.6875rem;

    --border-radius-24: 1.5rem;
    --border-radius-20: 1.25rem;
    --border-radius-16: 1rem;
    --border-radius-12: 0.75rem;
    --border-radius-8: 0.5rem;
    --border-radius-4: 0.25rem;


}

html.dark {
    --color-bg-dark: #1a1c35;
    --color-bg: #22243b;
    --color-grey: #4e4e4e;
    --color-text: #fdfffd;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    /*display: flex;*/
    /*flex-direction: column;*/
    /*justify-content: center;*/
    /*align-items: center;*/
    font-size: 1.2rem;
    accent-color: var(--color-accent);
    background: var(--color-white);
}

#root {
    width: 100%;
    height: 100%;
    max-width: 500px;
    background: var(--color-white);

    border-radius: 1rem;
    display: flex;
    flex-direction: column;

}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
}

ul {
    list-style: none;
    padding-left: 0;
}

button {
    outline: none;
    border: none;
}

::-webkit-scrollbar {
    /*width: 0.1rem;*/
}

::-webkit-scrollbar-track {
    background-color: var(--color-bg-dark);
}

::-webkit-scrollbar-thumb {
    background-color: var(--color-scrollbar);
}

::-webkit-scrollbar-thumb:hover {
    background-color: var(--color-accent);
}
