.wrapper {
    overflow-x: hidden;
    margin-bottom: 100px;
}

.dateWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 28px 16px;
    border-bottom: 1px solid var(--color-light-grey);
}

.dateTitle {
    font-family: Pretendard-Regular;
    font-size: var(--font-size-15);
    font-weight: 500;
}

.date {
    font-family: Pretendard-Regular;
    font-size: var(--font-size-19);
    font-weight: 700;
}

.button {
    background: transparent;
}

.timeZone {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 8px;
    gap: 4px;

}
.desc {
    font-family: Pretendard-Regular;
    font-size: var(--font-size-15);
    font-weight: 500;
    color: var(--color-grey);
    text-align: center;
    margin: 8px 0;
}

.timeButton {
    width: 49%;
    background: white;
    border: 1px solid var(--color-light-grey);
    border-radius: 12px;
    padding: 12px 16px;
    box-shadow: 0px 3.816793918609619px 15.267175674438477px 0px #1B202B0D;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Pretendard-Regular;
    font-weight: 700;
    gap: 8px;
}

.buttonWrapper {
    padding: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px -4px 16px 0px #00000014;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: var(--color-white);
}

.numberBoxWrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    padding: 8px;
}

.selected {
    background: var(--color-black);
    color: white;
}


.usageWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 16px;
    gap: 8px;
}
.usageButton {
    width: 50%;
    padding: 12px 24px;
    box-sizing: border-box;
    height: 64px;
    border: 2px solid var(--color-light-grey);
    background: var(--color-white);
    border-radius: var(--border-radius-12);
    font-family: Pretendard-Regular;
    font-size: var(--font-size-16);
    font-weight: 500;
}
.timeUsedWrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0px;
}
.timeUsedWrapper > *:nth-child(3n+1) {
    border-right: 1px solid var(--color-light-grey); /* 첫 번째 항목의 오른쪽 테두리 */
    border-bottom: 1px solid var(--color-light-grey); /* 첫 번째 항목의 오른쪽 테두리 */
}
.timeUsedWrapper > *:nth-child(3n+2) {
    border-bottom: 1px solid var(--color-light-grey); /* 첫 번째 항목의 오른쪽 테두리 */
}

.timeUsedWrapper > *:nth-child(3n) {
    border-left: 1px solid var(--color-light-grey); /* 세 번째 항목의 왼쪽 테두리 */
    border-bottom: 1px solid var(--color-light-grey); /* 첫 번째 항목의 오른쪽 테두리 */
}
