.wrapper {
    width: 100%;
    min-width: 0;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    padding: 32px 0;
}
.title {
    font-family: Pretendard-Regular;
    font-size: var(--font-size-21);
    font-weight: 700;
    margin: 0 16px 12px 16px;

}
.cardListWrapper {
    display: flex;
    height: 100%;
    flex-wrap: wrap;
}

.cardListWrapper > *:nth-child(odd) {
    border-left: 0;
}

.cardListWrapper > *:nth-child(even) {
    border-right: 0;
}
